html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article, aside, footer, header, nav, section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

figcaption, figure, main {
  display: block;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: inherit;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio, video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button, input, optgroup, select, textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button, input {
  overflow: visible;
}

button, select {
  text-transform: none;
}

button, html [type=button], [type=reset], [type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner, [type=button]::-moz-focus-inner, [type=reset]::-moz-focus-inner, [type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring, [type=button]:-moz-focusring, [type=reset]:-moz-focusring, [type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox], [type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button, [type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-cancel-button, [type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details, menu {
  display: block;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

html {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-size: 1.6rem;
  font-family: "Ubuntu", sans-serif;
  background: #f8f8ff;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-family: "Spartan", sans-serif;
  color: #3F3F48;
  font-weight: 700;
}

h2 {
  text-transform: uppercase;
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

img {
  width: 100%;
  display: block;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: black;
  font-family: "Ubuntu", sans-serif;
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 3vw;
}

@keyframes flickerAnimation {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.arrowContainer {
  text-align: center;
  padding: 10px 0;
}

.arrowContainer i {
  color: #ef4c0a;
  font-size: 2.5rem;
  animation: flickerAnimation 2s infinite;
}

.arrowContainer a:hover i,
.arrowContainer a:focus i {
  animation: flickerAnimation 0s 1;
}

header {
  height: 100vh;
  background: #C4FBFF;
}
header .mainNav {
  background: #05B6C3;
  height: 10vh;
  border-bottom: 4px solid teal;
}
header .mainNav ul {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
header .mainNav .navListItem {
  padding: 0 20px;
}
header .mainNav .bars {
  display: none;
  padding: 20px;
}
header .mainNav button {
  background: #05B6C3;
  border: none;
  cursor: pointer;
}
header .mainNav button:hover, header .mainNav button:focus {
  opacity: 1;
}
header .mainNav button i {
  font-size: 3rem;
  color: #3F3F48;
}
header .mainNav a {
  color: black;
  font-weight: 700;
  font-size: 1.8rem;
}
header .mainNav a:hover, header .mainNav a:focus {
  border-bottom: solid 5px #ef4c0a;
}
header .mainNav.inactive {
  display: none;
}
header .headerSectionContainer {
  height: 85vh;
  display: flex;
  position: relative;
  overflow: hidden;
}
header .name {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 3vw;
}
header .name h1 {
  color: #242424;
  font-size: 6rem;
  text-transform: uppercase;
  letter-spacing: -1px;
  transition: all 0.6s ease-in;
}
header .name p {
  font-weight: 700;
  color: #3F3F48;
  font-size: 3rem;
  transition: all 0.6s ease-in;
}
header .name,
header .circles {
  flex: 1 1 50%;
}
header .circle {
  border-radius: 50%;
}
header .circleOne,
header .circleThree {
  background: #05B6C3;
  position: absolute;
  width: 400px;
  height: 400px;
  transition: all 0.7s ease-in;
  border: teal 4px solid;
}
header .circleOne {
  top: -70px;
  right: 280px;
  z-index: 10;
}
header .circleThree {
  top: 200px;
  right: 220px;
  z-index: 30;
}
header .circleTwo {
  position: absolute;
  width: 600px;
  height: 600px;
  top: -90px;
  right: -130px;
  background: #40BEC8;
  z-index: 40;
  transition: all 1s ease-in;
  border: teal 6px solid;
}
header .arrowContainer {
  height: 5vh;
}

.about {
  background: #05B6C3;
}
.about .aboutContainer {
  display: flex;
  align-items: center;
}
.about .aboutLeft {
  flex: 1 1 40%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.about .aboutImageContainer {
  width: 20vw;
  position: relative;
  z-index: 10;
}
.about .aboutImageContainer::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  border-top: 80px solid transparent;
  border-left: 80px solid #C4FBFF;
  transform: rotate(90deg);
  top: -20px;
  left: -20px;
  z-index: -10;
}
.about .aboutImageContainer::after {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  border-top: 80px solid transparent;
  border-left: 80px solid #C4FBFF;
  transform: rotate(270deg);
  bottom: -20px;
  right: -20px;
  z-index: -10;
}
.about .aboutRight {
  flex: 1 1 60%;
}
.about .aboutRight h2 {
  margin: 0 0 30px 0;
}
.about .aboutRight h2 span {
  position: relative;
}
.about .aboutRight h2 span::after {
  display: block;
  content: "";
  background-image: url(../assets/icons8-squiggly-line-100-dark-grey.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 65%;
  position: absolute;
  transform: rotate(45deg);
  width: 100px;
  height: 100px;
  bottom: -70px;
  left: 27%;
}
.about .circle {
  position: relative;
  display: block;
  margin: 2em 0;
  background-color: transparent;
  color: black;
  text-align: center;
}
.about .circle:after {
  display: block;
  padding-bottom: 100%;
  width: 100%;
  height: 0;
  border-radius: 50%;
  background-color: #40BEC8;
  content: "";
}
.about .circleInner {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.about .circleWrapper {
  display: table;
  width: 100%;
  height: 100%;
}
.about .circleContent {
  font-size: 2rem;
  letter-spacing: -1px;
  line-height: 1.3;
  display: table-cell;
  padding: 3em;
  vertical-align: middle;
}
.about .circleContent p {
  padding: 10px 0;
}
.about .arrowContainer {
  padding: 10px 0;
}

.whatIsTMS {
  background: #C4FBFF;
}
.whatIsTMS h2 {
  text-align: center;
  padding: 100px 0 60px 0;
}
.whatIsTMS h2 span {
  position: relative;
  z-index: 10;
}
.whatIsTMS h2 span::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  border-top: 100px solid transparent;
  border-left: 100px solid #40BEC8;
  transform: rotate(270deg);
  top: -40px;
  right: -10px;
  z-index: -10;
}
.whatIsTMS h2 span::after {
  display: block;
  content: "";
  background-image: url(../assets/icons8-squiggly-line-100-dark-grey.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 65%;
  position: absolute;
  transform: rotate(45deg);
  width: 100px;
  height: 100px;
  top: 0px;
  right: -60px;
}
.whatIsTMS .project {
  display: flex;
