}
.whatIsTMS .projectText {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 3vw;
}
.whatIsTMS .projectText p {
  width: 90%;
  line-height: 1.5;
  letter-spacing: -0.5px;
  margin: 10px 0 0 0;
}
.whatIsTMS .projectText a {
