}
.whatIsTMS .projectSkills {
  display: flex;
}
.whatIsTMS .projectSkills li {
  padding: 10px;
}
.whatIsTMS .projectSkills i {
  font-size: 3rem;
}
.whatIsTMS .projectSkills img {
  width: 3rem;
}
.whatIsTMS .arrowContainer {
  padding: 10px 0;
  background-color: #D0F2F4;
}

.doIHaveTMS h2 {
  padding: 100px 0 60px 0;
  text-align: center;
}
.doIHaveTMS h2 span {
  position: relative;
}
.doIHaveTMS h2 span::before {
  content: "";
  width: 0;
