}
.techniques h2, .techniques h3 {
  text-align: center;
}
.techniques h2 {
  padding: 100px 0 60px 0;
}
.techniques h3 {
  padding: 30px 0 10px 0;
}
.techniques p {
  margin: 0 0 10px 0;
}
.techniques a {
  text-decoration: underline #ef4c0a;
}
.techniques a:hover {
  color: #ef4c0a;
}
.techniques li {
  margin: 0 0 10px 0;
}

.contact {
  display: flex;
  padding: 100px 0 60px 0;
  background: #D0F2F4;
}
.contact .contactTitle,
.contact .contactForm {
  flex: 1 1 50%;
  display: flex;
  flex-direction: column;
}
.contact .contactTitle {
  align-items: center;
  position: relative;
}
.contact .contactTitle h2 {
  padding: 20px 0;
}
.contact .contactTitle h2 span {
  position: relative;
  z-index: 10;
}
.contact .contactTitle h2 span::before {
  content: "";
  width: 0;
  height: 0;
  position: absolute;
  border-top: 100px solid transparent;
  border-left: 100px solid #40BEC8;
  transform: rotate(270deg);
  top: -40px;
  right: -10px;
  z-index: -10;
}
.contact .contactTitle h2 span::after {
  display: block;
  content: "";
  background-image: url(../assets/icons8-squiggly-line-100-dark-grey.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 65%;
  position: absolute;
  transform: rotate(45deg);
  width: 100px;
  height: 100px;
  bottom: -55px;
  right: -50px;
}
.contact .contactTitle img {
