footer {
  background: #05B6C3;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 0;
}
footer .disclaimer {
  max-width: 800px;
  margin: 30px 0;
}
footer .disclaimer h4 {
  text-align: center;
}
footer .disclaimer p {
  padding: 0 10px;
}
footer .personal {
