}
footer .attributions {
  position: absolute;
  display: none;
  background: #C4FBFF;
  min-width: 160px;
  padding: 10px;
  z-index: 10;
  bottom: 28px;
  left: 0;
  border-radius: 4px;
  border: solid 1px black;
}
footer .thanks .icons,
footer .thanks .photos {
  padding: 20px 0 0 0;
}
footer .thanks p {
  padding: 5px;
}
footer .thanks a {
  text-decoration: underline;
}
footer .thanks a:hover, footer .thanks a:focus {
  text-decoration-color: #ef4c0a;
}

.modalNav {
  display: block;
  position: fixed;
  height: 100%;
  width: 100%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: #C4FBFF;
  z-index: 100;
}

.modalList {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.modalExit {
  align-self: flex-end;
  padding: 10px;
}
.modalExit:hover button, .modalExit:focus-within button {
  background: #ef4c0a;
}

.modalList button {
  border: none;
  background: #05B6C3;
  padding: 10px;
  color: white;
  border-radius: 4px;
  align-self: end;
  font-weight: bold;
  font-size: 2.2rem;
  cursor: pointer;
}

.modalList .modalListItem {
  padding: 10px 0;
}
.modalList .modalListItem:hover, .modalList .modalListItem:focus-within {
  text-decoration: underline #ef4c0a;
}

.modalList a {
  font-size: 2rem;
  padding: 10px 0;
}

.modalNav.inactive {
  display: none;
}

@media (max-width: 1280px) {
  .wrapper {
    padding: 0;
  }
}
@media (max-width: 1080px) {
  header .circleOne,
  header .circleThree {
    top: -90px;
    right: -130px;
    width: 0px;
    height: 0px;
  }
}
@media (max-width: 950px) {
