header {

  height: 100vh;
  background: $secondary;
 
  .mainNav {
    background: $primary;
    height: 10vh;
    border-bottom: 4px solid teal;
  }

  .mainNav ul {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .mainNav .navListItem {
    padding: 0 20px;
  }

  .mainNav .bars {
    display: none;
    padding: 20px;
  }

  .mainNav button {
    background: $primary;
    border: none;
    cursor: pointer;
    
    &:hover,
    &:focus {
      opacity: 1;
    }

  }

  .mainNav button i {
    font-size: 3rem;
    color: $dark-grey;
  }

  .mainNav a {
    color: black;
    font-weight: 700;
    font-size: 1.8rem;

    &:hover,
    &:focus {
      border-bottom: solid 5px $highlight;
    }
  }

  .mainNav.inactive {
    display: none;
  }
  
  .headerSectionContainer {
    height: 85vh;
    display: flex;
    position: relative;
    overflow: hidden;
  }

  .name {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 3vw;
  }

  .name h1 {
    color: $off-black;
    font-size: 6rem;
    text-transform: uppercase;
    letter-spacing: -1px;
    transition: all 0.6s ease-in;

  }

  .name p {
    font-weight: 700;
    color: $dark-grey;
    font-size: 3rem;
    transition: all 0.6s ease-in;
  }

  .name, 
  .circles {
    flex: 1 1 50%;
  }

  .circle {
    border-radius: 50%;
  }

  // COLOUR CIRCLES
  .circleOne,
  .circleThree {
    background: $primary;
    position: absolute;
    width: 400px;
    height: 400px;
    transition: all 0.7s ease-in;
    border: teal 4px solid;
  }

  .circleOne {
    top: -70px;
    right: 280px;
    z-index: 10;
    // opacity: 0.5;
  }

  .circleThree {
    top: 200px;
    right: 220px;
    z-index: 30;
    // opacity: 0.8;
  }

  // CIRCLE WITH IMAGE
  .circleTwo {
    position: absolute;
    width: 600px;
    height: 600px;
    top: -90px;
    right: -130px;
    background: $primary-light;
    z-index: 40;
    // filter: sepia(50%);
    // object-fit: cover;
    transition: all 1s ease-in;
    border: teal 6px solid;
  }

  .arrowContainer {
    height: 5vh;
  }

} // Header styling ends

